.header {
    background-color: $base-green;
    color: white;

    .container {
        display: flex;
        justify-content: space-between;
        align-items: stetch;
    }

    &__right {
        display: flex;
        align-items: center;
    }

    &__soc {
        display: flex;
        justify-content: space-between;
        align-items: center;
        &:after {
            content: '';
            display: block;
            height: 34px;
            width: 1px;
            background-color: #fff;
            margin-left: 25px;
        }
    }
    &__soc-item {
        padding: 0;
        margin: 0;
        margin-right: 14px;
        list-style: none;  

        a {
            display: flex;
        }

        &:last-child {
            margin-right: 0;
        }
    }
}